.search-wrap {
  position: relative;
}

.search-label {
  position: relative;
}
.search-label svg {
  position: absolute;
  top: 50%;
  left: 19px;
  margin-top: -1px;
  transform: translateY(-50%);
  pointer-events: none;
}

.search-input {
  width: 100%;
  padding: 18px 18px 18px 54px;
  color: #292B2D;
  font-size: 20px;
  font-size: calc(16px + (4) * (100vw - 400px) / (860));
  appearance: none;
  background-color: #F3F3F3;
  border: none;
  border-radius: 5px;
  outline: none;
}
@media (min-width: 1260px) {
  .search-input {
    font-size: 20px;
  }
}
@media (max-width: 400px) {
  .search-input {
    font-size: 16px;
  }
}
.open .search-input {
  background-color: #fff !important;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.1);
  border-radius: 5px 5px 0 0;
}
.search-input.small {
  padding: 14px 14px 14px 54px;
  background-color: rgba(0, 0, 0, 0.1);
}
.search-input::placeholder {
  color: #292B2D;
  opacity: 0.5;
}

.search-suggestions-wrap {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  display: none;
  background-color: #fff;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.1);
  border-radius: 0 0 5px 5px;
}
.open .search-suggestions-wrap {
  display: block;
}
.open .search-suggestions-wrap:before {
  content: "";
  position: absolute;
  bottom: 100%;
  left: 0;
  width: 100%;
  height: 10px;
  background-color: #fff;
}

.search-suggestion {
  border-top: 1px solid #E8E8E8;
  transition: background-color 0.15s ease;
}
.search-suggestion a, .search-suggestion .footer-container button, .footer-container .search-suggestion button {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  line-height: inherit;
  text-align: left;
  text-decoration: none !important;
}
.search-suggestion img {
  display: block;
  width: 30px;
  height: 30px;
  margin-right: 10px;
}
.search-suggestion div {
  line-height: 0.6em;
}
.search-suggestion strong {
  display: block;
  font-weight: bold;
  font-size: 14px;
  font-size: calc(12px + (2) * (100vw - 400px) / (860));
}
@media (min-width: 1260px) {
  .search-suggestion strong {
    font-size: 14px;
  }
}
@media (max-width: 400px) {
  .search-suggestion strong {
    font-size: 12px;
  }
}
